$compile-color: 0;

/// Replace `$search` with `$replace` in `$string`
/// @author Hugo Giraudel
/// @param {String} $string - Initial string
/// @param {String} $search - Substring to replace
/// @param {String} $replace ('') - New value
/// @return {String} - Updated string
@function str-replace($string, $search, $replace: '') {
    $index: str-index($string, $search);

    @if $index {
        @return str-slice($string, 1, $index - 1) + $replace + str-replace(str-slice($string, $index + str-length($search)), $search, $replace);
    }

    @return $string;
}

/// This function cleans and extracts color values from a given input.
/// @param {String} $input - The input string containing a color value.
/// @return {String} - The cleaned color value extracted from the input.
/// Usage Examples:
///
/// // Remove whitespace and extract color from hex format
/// $hexColor: color_clean(#FFA500); // Returns "FFA500"
///
/// // Remove whitespace and extract color from RGB format
/// $rgbColor: color_clean(rgb(255, 0, 0)); // Returns "255,0,0"
///
@function color_clean($input) {
    $x: str-replace(inspect($input), " ", "");

    @if str-index($x, "#")==1 {
        $x: str-slice($x, 2);
    }

    @else {
        $start-index: str-index($x, "(");
        $end-index: str-index($x, ")");

        @if $start-index !=null and $end-index !=null {
            $x: str-slice($x, $start-index + 1, $end-index - 1);
        }
    }

    @return $x;
}

/// This mixin sets a CSS property with a cleaned color value.
///
/// @param {String} $property - The CSS property name.
/// @param {String} $color - The input color value to be cleaned.
///
/// Usage Example:
///
/// // Usage of the rcolor_decl mixin to set a background color
/// .my-element {
///   @include rcolor_decl("background-color", "#FFA500");
/// }
///
@mixin _color_decl($property, $color) {
    @if $compile-color == 1 {
        #{$property}: color_clean($color);
    } @else {
        #{$property}: $color;
    }
}

/// This function generates a CSS variable for a given RGB property value.
///
/// @param {String} $property - The RGB property value.
/// @return {String} - A CSS variable for the RGB property.
///
/// Usage Example:
///
/// // Usage of the rcolor function to generate a CSS variable
/// $redColor: rcolor("255, 0, 0");
/// .my-element {
///   color: var($redColor);
/// }
///
@function _color($property) {
    @if $compile-color == 1 {
        $value: var($property);
        @return rgb($value);
    } @else {
        @return var($property);
    }
}

@mixin _no_drag() {
    -webkit-user-drag: none;
    -khtml-user-drag: none;
    -moz-user-drag: none;
    -o-user-drag: none;
    user-drag: none;
}

@mixin _no_select() {
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

@mixin _input_block() {
    display: block;
    width: 100%;
    max-width: 100%;
    min-width: 100%;
    resize: vertical;
    height: 100%;
}