$-c-icon: ic;
$-c-icon-close: close;
$-c-icon-remove: remove;
$-c-icon-expand: expand;
$-c-icon-shrink: shrink;


.#{$-c-icon} {
    $-c-default-size: 20px;

    min-height: $-c-default-size;
    min-width: $-c-default-size;
    display: inline-block;

    background-size: auto;
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: 50% 50%;

    &.#{$-c-icon-close},
    &.#{$-c-icon-remove} {
        background-image: url('data:image/svg+xml;base64,');
    }

    &.#{$-c-icon-expand} {
        background-image: url('data:image/svg+xml;base64,');
    }

    &.#{$-c-icon-shrink} {
        background-image: url('data:image/svg+xml;base64,');
    }



}