$-c-playground: playground;
$-c-playground-meta: meta;
$-c-playground-control: control;
$-c-playground-title: title;
$-c-playground-content: content;

$-c-playground-manager: playground-manager;


.#{$-c-playground} {
    $c-hsize: 30px;
    $c-hsize-half: calc($c-hsize / 2);

    background-color: _color($--color-foreground);
    color: _color($--color-text);
    box-shadow: 1px 1px 1px .5px _color($--color-shadow);
    //border: var(--playground-border);

    // can overrided by adding style like this:
    // <div style="--context-background-color: #fff; --context-text-color: #000;"></div>
    //color: var(--context-text-color);

    margin: $c-hsize-half 0;
    border-radius: $c-hsize-half;
    backface-visibility: hidden;

    >.#{$-c-playground-meta} {
        display: flex;
        height: $c-hsize;
        margin: 0 $c-hsize;
        gap: $c-hsize-half;

        flex-direction: row-reverse;

        >.#{$-c-playground-control} {
            $spacing: 1px;

            display: flex;
            margin: 0 calc($spacing * -1);

            >.action {
                align-self: center;
                box-shadow: none;
                height: calc($c-hsize / 1.2);
                width: calc($c-hsize / 1);
                margin: $spacing;
            }
        }

        >.#{$-c-playground-title} {
            flex-grow: 1;
            align-self: center;

            input {
                @include strip_input_attr();
                display: block;
                width: 100%;
                color: inherit;
            }
        }
    }

    >.#{$-c-playground-content} {
        padding: _gap(large);
    }
}

.#{$-c-playground-manager} {

    $c-default-height: 20px;
    $c-default-spacing: 5px;

    display: flex;

    background-color: _color($--color-foreground);
    padding: $c-default-spacing;
    gap: $c-default-spacing;

    >* {
        align-self: center;
        line-height: $c-default-height;
        //height: $c-default-height;
        padding: calc($c-default-height / 2);
    }
}