$-c-button: button;

.#{$-c-button},
button,
input[type=button],
input[type=submit] {
    @include _no_select();
    @include _no_drag();
    
    cursor: pointer;

    border: 1px solid _color($--color-border);
    border-radius: 3px;
    background-color: _color($--color-foreground-accent);
    color: _color($--color-text-accent);
}