@import "_utilities.scss";
@import "_variables.scss";

@import "_reset.scss";
@import "_theme.default.scss";

@import "_component_icon.scss";
@import "_component_playground.scss";
@import "_component_button.scss";

body {
    background-color: _color($--color-background);
    font-family: Overpass, -apple-system, BlinkMacSystemFont, Segoe UI, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}

textarea {
    border: 1px solid _color($--color-border);
    background-color: _color($--color-foreground);
    color: _color($--color-text);

    &:focus {
        outline: 1px dotted _color($--color-outline);
        outline-offset: 1px;
    }

    &.block {
        @include _input_block();
    }

    &.readonly {
        outline: none;
        //border-color: _color($--color-background);
    }
}

.wrapper {
    padding: 30px;
    max-width: 1200px;
    margin: auto;
}



.bg-chess {
    $size: 30px;
    $position: calc($size/2);
    $color-a: #454545;
    $color-b: #656565;

    @keyframes slide {
        0% {
            background-position: 0 0, -15 -15;
        }
    
        100% {
            background-position: 15 15, 0 0;
        }
    }

    background-color: $color-b;
    background-size: $size $size;
    background-position: 0 0, $position $position;
    background-image:
        linear-gradient(45deg, $color-a 25%, transparent 25%, transparent 75%, $color-a 75%, $color-a),
        linear-gradient(45deg, $color-a 25%, transparent 25%, transparent 75%, $color-a 75%, $color-a);

    &.animate {
        animation: slide 1s linear infinite;
    }
}



.context {
    display: flex;
    gap: 10px;
    flex-direction: row;

    &.vertical {
        flex-direction: column;
    }

    >.shrink {
        flex-shrink: 1;
    }

    >.grow {
        flex-grow: 1;
    }
}