:root {
    @include _color_decl($--color-background, rgba(49, 49, 49, 1));
    @include _color_decl($--color-foreground, rgba(64, 64, 64, 1));
    @include _color_decl($--color-foreground-grey, rgba(234, 234, 234, 1));
    @include _color_decl($--color-foreground-accent, rgba(0, 0, 0, 0));
    @include _color_decl($--color-text, rgb(207, 207, 207));
    @include _color_decl($--color-text-accent, rgba(251, 255, 54, 1));
    @include _color_decl($--color-shadow, rgba(0, 0, 0, 0.1));
    @include _color_decl($--color-border, rgba(83, 83, 83, 1));
    @include _color_decl($--color-outline, rgb(251, 255, 54));
}

$-theme-border : 2px solid _color($--color-foreground);