
// disini dideklarasikan nama-nama variable biar terlihat keren
// nanti kalo ada mau perubahan nama variabel, tinggal di-update disini
$--color-background: --color-background;
$--color-foreground: --color-foreground;
$--color-foreground-grey: --color-foreground-grey;
$--color-foreground-accent: --color-foreground-accent;
$--color-text: --color-text;
$--color-text-accent: --color-text-accent;
$--color-border: --color-border;
$--color-outline: --color-outline;

$--color-shadow: --color-shadow;
$--color-background-b: --col-bg-b;


@function _gap($param: "normal") {
    @if $param == "small" {
        @return 5px;
    }
    @if $param == "normal" {
        @return 10px;
    }
    @if $param == "large" {
        @return 20px;
    }
}

@mixin strip_input_attr() {
    border: 0;
    background-color: inherit;
    outline: none;
    color: inherit;
}